export const url = "/api/e9b849a515a84327b424af7ccdbf2949/mobile/common/v1_0_0/coop/collect"
export const verifyRequest = async function (data) {
    return await request({
        url,
        method: "POST",
        data: {
            version: "1.0.0",
            content: {
                platform: "web",
                ...data
            },
        },
    })
}
